<template>
  <div id="addCourseTable">
      <table>
        <tr>
            <th style="width: 220px;">课程</th>
            <th style="width: 70px;">学分</th>
            <th style="width: 70px;">成绩<br>(A-F)</th>
            <th style="width: 70px;">成绩<br>(0-100)</th>
            <th style="width: 70px;">绩点<br>(0.0-4.0)</th>
        </tr>
        <tr v-for="(course, idx) in courses" v-bind:key="idx">
            <td>{{course.name}}</td>
            <td>{{course.credits}}</td>
            <td>{{course.letterGrade}}</td>
            <td>{{course.percentage}}</td>
            <td>{{course.gradepts}}</td>
            <td style="background-color:white;"> 
                <div class="close small-x" v-on:click="deleteCourse(idx)"></div>
            </td>
        </tr>
        </table>
        <input type="text" v-model="courseNumber">
        <button @click="addCourses">添加课程</button>
  </div>
</template>

<script>

export default {
  props: {
      courses: Array
  },
  data() {
      return {
        //   localCourses
        courseNumber: 1
      }
  },
  created() {
      
  },
  methods: {
      deleteCourse(idx) {
          this.courses.splice(idx, 1);
          if (this.courses.length == 0) {
              this.courses.push({
                name: '',
                credits: 0,
                letterGrade:'',
                percentage: 0,
                gradepts: 0
              })
          }
      },
      addCourses() {
          for (var i = 0; i < this.courseNumber; i++)
            this.courses.push({
                name: '',
                credits: 0,
                letterGrade:'',
                percentage: 0,
                gradepts: 0
            })
      }
  }


}
</script>

